* {
    outline: none;
    padding: 0;

    &::after {
        margin : 0;
        padding: 0;
    }

    &::before {
        margin : 0;
        padding: 0;
    }
}

body {
    line-height: 1.6;
	color:$body-color;
	font-size: 15px;
	font-family: var(--font-family-base);
	background-color: #e9e9e9;
	
    &.fixed{
        overflow: hidden;
        position: fixed;
    }
}

h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a{
	color: var(--title);
}
h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6{
	font-family:var(--font-family-base);
	font-weight:$headings-font-weight;
	color:var(--title);
}

h1,
.h1{
	line-height:1.3;
	@include respond('tab-port'){
		font-size: 2rem;
	}
	@include respond('phone-land'){
		font-size: 1.875rem;
	}
}
h2,
.h2{
	line-height:1.4;
	@include respond('tab-port'){
		font-size: 1.75rem;
	}
	@include respond('phone-land'){
		font-size: 1.5rem;
	}
}
h3,
.h3{
	line-height:1.4;
	@include respond('phone-land'){
		font-size: 1.375rem;
	}
}
h4,
.h4{
	line-height:1.4;
	@include respond('phone-land'){
		font-size: 1.25rem;
	}
}
h5,
.h5{
	line-height:1.35;
	@include respond('phone-land'){
		font-size: 1.125rem;
	}
}
h6,
.h6{
	line-height:1.5;
	@include respond('phone-land'){
		font-size: 1rem;
	}
}

ul {
    padding: 0;
    margin : 0;
}
img {
    border-style: none;
    height: auto;
    max-width: 100%;
    vertical-align: middle;
}
li {
    list-style: none;
}

a {
    text-decoration: none;
    outline        : none;
	color          : var(--primary);	
	@include transitionMedium;	
    &:hover,
    &:focus,
    &.active {
        text-decoration: none;
        outline        : none;
		color:var(--primary-hover);
    }
}
// COLOR
.text-primary{
	color:var(--primary) !important; 	
}
.bg-primary {
    background-color: var(--primary) !important;
}
b,
strong,
.strong {
    font-weight: 500;
    color      :var(--title);
}

.h-0 {
    height: 0;
}

#main-wrapper {
    opacity       : 0;
    transition    : all 0.25s ease-in;
    overflow      : hidden;
    position      : relative;
    z-index       : 1;
    margin-top    : 60px;

    &.show {
        opacity: 1;
    }
}

.content-body {
    margin-left  : 65px;
    margin-top   : 18px;
    margin-right : 6px;
    margin-bottom: 45px;
}

.container-fluid{
	padding-left:80px;
	padding-right:80px;
	
	@include respond('wide-desktop'){
		padding-left:40px;
		padding-right:40px;
	}
	@include respond('tab-land'){
		padding-left:20px;
		padding-right:20px;
	}
	@include respond('phone'){
		padding-left:15px;
		padding-right:15px;
	}
}
.bg-gray{
	background-color: #f8f8f8;
}

// Selection
