// Pointer
#pointer-dot {
    top:-4px;
    left:-4px;
    width: 12px;
    height: 12px;
    position: fixed;
    border-radius: 50px;
    z-index: 999998;
    pointer-events: none;
    transition: border-color 0.5s;
    background: var(--primary);
	box-shadow: 0 0 0 2px rgba(255,255,255,0.2);
	transition: all 0.1s ease 0s;
}
#pointer-ring {
    left: 0;
    top: 0;
    width: 25px;
    height: 25px;
    display:block;
    border: 1px solid var(--primary) !important;
    position: fixed;
    border-radius: 100px;
    z-index: 999999;
    pointer-events: none;
	transition: width 0.3s, height 0.3s, left 0.3s, left 0.3s;
	display:none;
}
#pointer-dot.active {
    opacity:0.3;
}

