.page-title {
    padding: 65px 0px 45px;
    position: relative;
    background: $light;

    background-size: cover;
    background-position: center;

    p {
        font-size: 20px;
        font-weight: 500;
        margin-bottom: 5px;
        color: #7272A8;
        text-transform: capitalize;

        @include respond('phone') {
            font-size: 16px;
        }
    }

    h3 {
        font-size: 40px;
        line-height: 1.2;
        font-weight: 600;
        text-transform: capitalize;

        @include respond('phone') {
            font-size: 28px;
        }
    }
